@import "../../../static/css/index.scss";
.mynote {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
}
.header {
  width: 100%;
  height: px2rem(64);
  background: #c1b696;
  display: flex;
  align-items: center;
  font-size: px2rem(16);
  color: #fff;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  .am-icon-md {
    margin-left: 8px;
  }
  span:nth-of-type(2) {
    font-size: px2rem(14);
    margin-right: 14px;
  }
  .add {
    //     width: px2rem(14);
    //     height: px2rem(14);
  }
}
.content {
  margin-top: px2rem(64);

  display: flex;
  flex-direction: column;
  li {
    display: flex;
    height: px2rem(46);
    align-items: center;
    border-bottom: 1px solid #e2e2da;
    justify-content: space-between;
    padding: 0 20px;
    font-size: px2rem(12);
    color: #474747;
    letter-spacing: 0;
    span:nth-of-type(1) {
      background: #e0d0bb;
      width: px2rem(13);
      height: px2rem(13);
      border-radius: 100%;
    }
    span:nth-of-type(2) {
      width: px2rem(250);
      margin-left: px2rem(-50);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
