@import "../../static/css/index.scss";
.homeContainer {
  width: 100%;
  height: 100%;
  background: url("../../static/images/home_bg@2x.png") no-repeat center;
  // bacground-size是写在baground下的位置才生效 --以前踩的坑
  background-size: 100% 100%;
  .homeReading {
    width: 100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      width: px2rem(155);
      height: px2rem(46);
      margin-top:px2rem(300);
      opacity: 0.6;
      font-size: px2rem(18);
      line-height: px2rem(46);
      font-family: PingFangSC-Regular;
      color: #ffffff;
      letter-spacing: 0;
      text-align: center;
      .am-button-primary{
        border:1px solid transparent !important;
        outline: none;
        box-shadow: 0 10px 14px 0;
        border-radius: px2rem(26.5);
        background-image:linear-gradient(-180deg, #c5ba9c 0%, #c1b696 100%);
      }
      .am-button-primary::before{
        border:1px solid transparent !important;

      }
      &.continue {
        margin-left: px2rem(15);
      }
    }
  }
}
