.mui-switch {
  width: 0.9rem;
  height: 0.5rem;
  position: relative;
  border: 0.025rem solid #c1b696;
  background-color: transparent;
  box-shadow: #c1b696 0 0 0 0 inset;
  border-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  background-clip: content-box;
  display: inline-block;
  -webkit-appearance: none;
  user-select: none;
  outline: none; }
  .mui-switch:before {
    content: "";
    width: 0.45rem;
    height: 0.45rem;
    position: absolute;
    top: 0px;
    left: 0;
    border-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    background-color: #c1b696;
    box-shadow: 0 0.025rem 0.075rem rgba(0, 0, 0, 0.4); }
  .mui-switch:checked {
    border-color: #828232;
    box-shadow: #828232 0 0 0 16px inset;
    background-color: #828232; }
    .mui-switch:checked:before {
      left: 0.425rem; }
  .mui-switch.mui-switch-animbg {
    transition: background-color ease 0.4s; }
    .mui-switch.mui-switch-animbg:before {
      transition: left 0.3s; }
    .mui-switch.mui-switch-animbg:checked {
      box-shadow: red 0 0 0 0 inset;
      background-color: #828232;
      transition: border-color 0.4s, background-color ease 0.4s; }
      .mui-switch.mui-switch-animbg:checked:before {
        transition: left 0.3s; }
  .mui-switch.mui-switch-anim {
    transition: border cubic-bezier(0, 0, 0, 1) 0.4s, box-shadow cubic-bezier(0, 0, 0, 1) 0.4s; }
    .mui-switch.mui-switch-anim:before {
      transition: left 0.3s; }
    .mui-switch.mui-switch-anim:checked {
      box-shadow: #828232 0 0 0 0.4rem inset;
      background-color: #828232;
      transition: border ease 0.4s, box-shadow ease 0.4s, background-color ease 1.2s; }
      .mui-switch.mui-switch-anim:checked:before {
        transition: left 0.3s; }
