@import "../../../static/css/index.scss";
.details {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #e8e8e4;
  .d-header {
    width: 100%;
    height: px2rem(64);
    background: #c1b696;
    display: flex;
    align-items: center;
    font-size: px2rem(16);
    color: #fff;
    justify-content: space-between;
    margin-bottom: 14px;
    .del {
      margin-right: 8px;
      width: 20px;
      height: 20px;
      background: url("../../../static/images/delete.png") no-repeat;
      background-size: 20px 20px;
    }
  }
  .noteContent {
    width: 100%;
    font-size: 14px;
    box-sizing: border-box;
    padding: 0 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    .am-textarea-control {
      flex: 1 1;
      padding-top: 0;
      padding-bottom: 0;
    }
    .am-list-item {
      padding-left: 0px;
    }
    textarea {
      background: #e8e8e4;
      width: px2rem(340);
      font-size: px2rem(14);
      color: #68686b;
      line-height: px2rem(40);
      letter-spacing: 1px;
      text-align: justify;
      text-indent: 2em;
    }
  }
}
